.wrap .user-header {
  overflow: hidden;
}
.wrap .user-header .avatar {
  display: flex;
  align-items: center;
  float: left;
  margin: 15px 0 0 15px;
}
.wrap .user-header .info {
  float: left;
  margin-left: 35px;
}
.wrap .user-header .info h1 {
  font-size: 32px;
  font-weight: bold;
}
.wrap .user-header .info .statistic .statistic-item {
  margin-left: 10px;
  display: inline-block;
  margin-right: 60px;
}
.wrap .collection-content {
  display: flex;
  flex-wrap: wrap;
  background-color: #ECECEC;
}
.wrap .collection-content .card {
  width: 180px;
  margin: 20px 15px;
}
.wrap .collection-content .card .ant-card-meta-description {
  color: rgba(0, 0, 0, 0.45);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
